.up-con{
	background-color: #fdd900;
	overflow: hidden;
}
.up-con .left{
	display: block;
	float: left;
	width: 5px;
	background-image: url("../images/shopcar-left_03.jpg");
	background-repeat: repeat-y;
	height: 60px;
	margin-top: 24px;
	margin-left: 50px;
	margin-bottom: 17px;
}
.up-con>.up-u{
	float: left;
	overflow:hidden;
	background-color: #fdd900;
}
.up-con>.up-u .car{
	float:left;
	font-size: 30px;
	line-height: 60px;
	margin-top: 24px;
	margin-left: 20px;
/*	width: 720px;*/
}
.up-con>.up-u .car>span{
	font-size: 22px;
}
.up-con>.up-u .car>span>span{
	color: red;
}
.up-con>ul{
	float: right;
	list-style: none;
}
.up-con>ul>li{
	margin-top: 38px;
	float: left;
	line-height: 36px;
	margin-right: 20px;
	font-size: 18px;
}
.shopcar .shopcarup .up-con>ul>.write-order444{
	border-bottom: 2px solid #000;
}
.shopcar .shopcarup .up-con>ul>.pay-success444{
	border-bottom: 2px solid #000;
}
.up-con>ul>li:nth-child(1){
	width: 55px;
	border-bottom: 5px solid #000;
	font-weight: bold;
}

.up-con>ul>li:nth-child(2){
	width: 72px;
}
.up-con>ul>li:nth-child(3){
	width: 126px;
	margin-right: 50px;
}

/*up完,up高144*/



